import Link from "next/link";

export const metadata = {
	title: "Ambulance Records",
};

// Server Component ‚Äî fetch data directly
export default async function DashboardPage() {
	// TODO: Fetch from your DB when ready
	// const ambulances = await prisma.ambulance.findMany({ orderBy: { vehicleNumber: "asc" } });
	const ambulances: any[] = []; // Empty array for now

	return (
		<div className="min-h-screen bg-gray-50">
			{/* Header */}
			<header className="bg-gradient-to-r from-[#2882FF] to-[#00BBA8] text-white py-4 px-6 flex items-center justify-between">
				<div className="flex items-center gap-3">
					<div className="text-2xl">üöë</div>
					<h1 className="text-lg font-semibold">
						Primecare General Hospital Management System
					</h1>
				</div>
				<button className="px-4 py-2 bg-white text-blue-600 rounded-lg font-medium hover:bg-gray-100 transition">
					‚Ü™ Logout
				</button>
			</header>

			{/* Tabs */}
			<div className="flex justify-center gap-4 my-6">
				<button className="px-8 py-3 bg-gradient-to-r from-[#00BBA8] to-[#2882FF] text-white rounded-full font-medium shadow-lg">
					üìã Records
				</button>
				<button className="px-8 py-3 bg-white text-gray-700 rounded-full font-medium shadow hover:shadow-lg transition">
					üìÑ Reports
				</button>
			</div>

			{/* Navigation Pills */}
			<div className="flex justify-center gap-6 mb-8">
				{["Ambulances", "Patients", "Locations", "Staffs", "Hospitals"].map(
					(item) => (
						<Link
							key={item}
							href="#"
							className="px-6 py-3 bg-white rounded-full shadow hover:shadow-md transition text-gray-700 font-medium"
						>
							{item}
						</Link>
					)
				)}
			</div>

			{/* Main Content */}
			<div className="max-w-7xl mx-auto px-6">
				<div className="bg-white rounded-2xl shadow-lg p-6">
					<div className="flex items-center justify-between mb-6">
						<div>
							<h2 className="text-xl font-semibold text-gray-800">
								Ambulance Record Management
							</h2>
							<p className="text-sm text-gray-500">
								Manage and track all ambulance vehicles in the fleet
							</p>
						</div>
						<button className="px-5 py-2 bg-blue-600 text-white rounded-lg font-medium hover:bg-blue-700 transition">
							+ Add Ambulance
						</button>
					</div>

					{/* Table */}
					<div className="overflow-x-auto">
						<table className="w-full text-left text-sm">
							<thead className="border-b border-gray-200">
								<tr className="text-gray-600">
									<th className="py-3 px-4 font-medium">Vehicle Number</th>
									<th className="py-3 px-4 font-medium">Type</th>
									<th className="py-3 px-4 font-medium">Status</th>
									<th className="py-3 px-4 font-medium">Current Location</th>
									<th className="py-3 px-4 font-medium">Driver</th>
									<th className="py-3 px-4 font-medium">Last Maintenance</th>
									<th className="py-3 px-4 font-medium">Actions</th>
								</tr>
							</thead>
							<tbody>
								{ambulances.length === 0 ? (
									<tr>
										<td colSpan={7} className="py-8 text-center text-gray-500">
											No ambulances found. Click "+ Add Ambulance" to get
											started.
										</td>
									</tr>
								) : (
									ambulances.map((amb) => (
										<tr
											key={amb.id}
											className="border-b border-gray-100 hover:bg-gray-50"
										>
											<td className="py-4 px-4 font-medium text-gray-800">
												{amb.vehicleNumber}
											</td>
											<td className="py-4 px-4 text-gray-600">{amb.type}</td>
											<td className="py-4 px-4">
												<span
													className={`px-3 py-1 rounded-full text-xs font-medium ${
														amb.status === "Available"
															? "bg-green-100 text-green-700"
															: amb.status === "On Mission"
															? "bg-blue-100 text-blue-700"
															: "bg-red-100 text-red-700"
													}`}
												>
													{amb.status}
												</span>
											</td>
											<td className="py-4 px-4 text-gray-600">
												{amb.currentLocation}
											</td>
											<td className="py-4 px-4 text-gray-600">{amb.driver}</td>
											<td className="py-4 px-4 text-gray-600">
												{amb.lastMaintenance}
											</td>
											<td className="py-4 px-4 flex gap-2">
												<button className="p-2 hover:bg-gray-100 rounded">
													‚úèÔ∏è
												</button>
												<button className="p-2 hover:bg-gray-100 rounded">
													üóëÔ∏è
												</button>
											</td>
										</tr>
									))
								)}
							</tbody>
						</table>
					</div>
				</div>
			</div>

			{/* Footer */}
			<footer className="bg-gradient-to-r from-[#2882FF] to-[#00BBA8] text-white text-center py-4 mt-12">
				Primecare Hospital ¬© 2025. All Rights Reserved
			</footer>
		</div>
	);
}
